* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
}

body {
  background-color: #000;
  font-family: "Inter", sans-serif;
}

#root {
  max-width: 550px;
  margin: 0 auto;
  padding: 44px 0;
  background-color: #23252c;
}

.app {
  max-width: 317px;
  margin: 0 auto;
  background-color: #1a1b21;
  border-radius: 10px 10px;
}

.info-image img {
  width: 317px;
  border-radius: 10px 10px 0 0;
  filter: brightness(80%);
}

.info-content {
  text-align: center;
}

.info-content h1 {
  color: White;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  margin: 21px 0 6px;
}

.info-content h2 {
  color: #f3bf99;
  font-weight: 400;
  font-size: 12.8px;
}

.info-content a {
  display: block;
  margin-top: 8.81px;
  color: #f5f5f5;
  font-weight: 400;
  font-size: 10.24px;
  line-height: 15px;
}

.info-btns {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 15px 0 33px;
}

.info-btns a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 115px;
  height: 34px;
  padding: 9px 13px 9px 11px;
  border-radius: 6px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}

.info-btns a.email {
  background-color: #fff;
  color: #374151;
  font-size: 14px;
  margin-right: 8.5px;
}

.info-btns a.linkedin {
  background-color: #5093e2;
  color: #ffffff;
  margin-left: 8.5px;
}

.info-btns a img {
  width: 15px;
  height: 15px;
  object-fit: cover;
  margin-right: 8px;
  overflow-x: hidden;
}

h3 {
  color: #f5f5f5;
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
}

.about,
.interest {
  padding: 0 34px 0 35px;
  margin-bottom: 27px;
}

.about p,
.interest p {
  font-weight: 400;
  font-size: 10.24px;
  line-height: 15px;
  color: #dcdcdc;
}

.footer {
  background-color: #161619;
  padding: 20px 0;
  border-radius: 0 0 10px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer > a img {
  width: 25px;
  height: 25px;
  margin-inline: 12px;
  cursor: pointer;
}
